		]
		const _endTime = (new Date(this.endTime.replace(/-/g,'/')).getTime()) / 1000
		this.countdowm(_endTime) //执行倒计时函数
	},
	computed: {
		showPeopleImg() {
			return this.list.length > 0
		}
	},
	methods: {
		toList() {
			
		},
		clickBanner(item) {
			console.log(item)
			if (this.inWhiteList) { //如果在小程序的网址白名单中，会走内置webview打开，否则会复制网址提示在外部浏览器打开
				uni.navigateTo({
					url: '/pages/out/out?url=' + item.webUrl
				});
			} else {
				uni.setClipboardData({
					data: item.webUrl
				});
				uni.showModal({
					content: '本网址无法直接在小程序内打开。已自动复制网址，请在手机浏览器里粘贴该网址',
					showCancel: false
				});
			}
		},
		// 地图导航
		toMap() {
			const that = this
			uni.openLocation({
					latitude: Number(that.lat),
					longitude:Number( that.long),
					name: that.venueName,
					address: that.address,
					scale: 13,
					success: function () {
							console.log('success');
					},
					fail: function () {
							console.log('fail');
					}
			});
		},
		// 拨打电话
		hanleCall() {
			uni.makePhoneCall({
			    phoneNumber: this.organizerPhone
			});
		},
		checked() {
			this.isChecked = !this.isChecked
		},
		toEscapeClause() {
			
		},
		 // 倒计时
		countdowm(timestamp) {
			// console.log(timestamp)
			let self = this
			let timer = setInterval(function() {
				let nowTime = new Date();
				// let endTime = new Date(timestamp * 1000);
				let endTime = timestamp * 1000;
				// console.log(endTime)
				let t = endTime - nowTime.getTime();
				if(t > 0) {
					let day = Math.floor(t / 86400000)
					let hour = Math.floor((t / 3600000) % 24)
					let min = Math.floor((t / 60000) % 60);
					let sec = Math.floor((t / 1000) % 60);
					hour = hour < 10 ? "0" + hour : hour;
					min = min < 10 ? "0" + min : min;
					sec = sec < 10 ? "0" + sec : sec;
					let format = '';
					if(day > 0){
						format = `${day}天${hour}小时${min}分${sec}秒`;
					} 
					if(day <= 0 && hour > 0 ){
						format = `${hour}小时${min}分${sec}秒`; 
					}
					if(day <= 0 && hour <= 0){
						format =`${min}分${sec}秒`;
					}
					self.content = format;
					self.isTosignUp = true
				} else{
					self.isTosignUp = false
					clearInterval(timer);
					self.content = '报名结束';
					self._callback();
				}
			},1000)
		},
		_callback(){
			if(this.callback && this.callback instanceof Function){
				console.log(...this)
				this.callback(...this);
			}
		},
		submit() {
			
		}
	}
}
